<template>
    <section :class="[index % 2 == 0 ? 'bg-1' : 'bg-2', 'border-bottom p-5']">
        <div class="container-fluid row">
            <div class="container d-flex flex-column col-12 col-md-6">
                
                <video v-if="obj.vid_src" :poster="obj.img_src" class="mb-auto ms-auto" controls>
                    <source :src="obj.vid_src" type="video/mp4">
                    Your browser does not support the video tag.
                </video>
                <img v-else :src="obj.img_src" class="section-img" alt="">
            </div>
            <div class="container d-flex flex-column col-12 col-md-6 mt-3 mt-md-0">
                <h3 class="text-dark fw-bolder" style="--bs-text-opacity: .75;">{{ obj.title }}</h3>
                <!-- For each detail -->
                <template v-for="(detail, index) in obj.details" :key="index">
                    <span class="text-body">
                        <!-- If its a single links -->
                        <template v-if="detail.key === 'Link'">
                            <strong>{{ detail.key }}: </strong>
                            <a :href="detail.text" target="_blank">url</a>
                        </template>
                        <!-- If its not a single links -->
                        <template v-else>
                            <!-- If not group of links -->
                            <template v-if="!detail.isLinks">
                                <strong>{{ detail.key }}: </strong>
                                {{ detail.text }}
                            </template>
                            <!-- If is group of links -->
                            <template v-else>
                                <strong>{{ detail.key }}: </strong>
                                <template v-for="(link, index) in detail.links" :key="index">
                                    <text v-if="index != 0">, </text><a :href="link.link" target="_blank">{{ link.text }}
                                    </a>
                                </template>
                            </template>
                        </template>
                    </span>
                </template>
                <span v-if="obj.link" class="mt-3 mt-md-auto"><a :href="obj.link" target="_blank"><button
                            class="btn btn-primary">View
                            project</button></a></span>
            </div>
        </div>
    </section>
</template>

<script>
export default {
    name: 'SectionCard',
    props: {
        obj: Object,
        index: Number
    },
    data() {
        return {

        }
    },
}

</script>

<style scoped>
.section-img {
    min-height: 15rem;
    object-fit: cover;
}

video{
    max-width: 640px;
    width: 100%;

}
</style>