<template>
    <div v-masonry="containerId" class="align-self-center" fit-width="true" transition-duration="0.3s" item-selector=".item">
        <div v-masonry-tile class="item m-2 point text-center border border-secondary rounded-3 p-3" data-bs-toggle="modal"
            data-bs-target="#exampleModal" v-for="(obj, index) in objs" :key="index" @click="setModal(obj)">
            <img :src="obj.img_src" class="gal-image" alt="">
            <p class="fs-6 fw-light mb-0 mt-2"> {{ obj.title }} </p>
        </div>
    </div>

    <!-- Modal -->
    <div class="modal modal-xl fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog">
            <div class="modal-content">
                <div class="modal-header">
                    <h1 class="modal-title fs-5" id="exampleModalLabel">{{ title }}</h1>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                </div>
                <div class="container-fluid row p-3">
                    <div class="container d-flex flex-column col-12 col-md-6">
                        <span v-for="(src,index) in images" class="mb-2" :key="index"><img :src="src" class="section-img" alt=""></span>
                    </div>
                    <div class="container d-flex flex-column col-12 col-md-6 mt-3 mt-md-0">
                        <!-- For each detail -->
                        <template v-for="(detail, index) in details" :key="index">
                            <span class="text-body">
                                <!-- If its a single links -->
                                <template v-if="detail.key === 'Link'">
                                    <strong>{{ detail.key }}: </strong>
                                    <a :href="detail.text" target="_blank">url</a>
                                </template>
                                <!-- If its not a single links -->
                                <template v-else>
                                    <!-- If not group of links -->
                                    <template v-if="!detail.isLinks">
                                        <strong>{{ detail.key }}: </strong>
                                        {{ detail.text }}
                                    </template>
                                    <!-- If is group of links -->
                                    <template v-else>
                                        <strong>{{ detail.key }}: </strong>
                                        <template v-for="(link, index) in detail.links" :key="index">
                                            <text v-if="index != 0">, </text><a :href="link.link" target="_blank">{{
                                                link.text }}
                                            </a>
                                        </template>
                                    </template>
                                </template>
                            </span>
                        </template>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    name: 'GalleryComponent',
    props: {
        objs: Array[Object]
    },
    data() {
        return {
            title: 'Test',
            details: {},
            link: '',
            img_src: '',
            images: []
        }
    },
    methods: {
        setModal(obj) {
            this.details = obj.details
            this.link = obj.link
            this.img_src = obj.img_src
            this.title = obj.title
            this.images = obj.images
        }
    },
}

</script>

<style scoped>
.gal-image,
.gal-card {
    max-width: 200px;
}
.point {
  transition: transform 0.3s ease-in-out;
  cursor: pointer;
}

.point:hover {
  transform: scale(1.1);
}
</style>